import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import ToolbarRenderNext from './toolbarRenderNext';
import { ToolbarRenderProps } from './interface';
import { usePlugins } from '../PluginsContext';

export function NewToolbar(props: ToolbarRenderProps) {
  const controller = Controller.useController({ maxPending: 10 });
  const plugins = usePlugins();

  return <ToolbarRenderNext {...props} controller={controller} plugins={plugins} />;
}
